.email-verified__container {
    background-color: rgb(180, 92, 33);
    padding: 12rem;
}

.email-verified__content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.email-verified__content > h1 {
    padding: 2rem;
    color: white;
}

.email-verified__content > h2 {
    color: white;
}

/* MEDIA QUERIES */
@media only screen and (max-width: 900px) {
    .email-verified__container {

        padding: 6rem 1rem;

    }
    
}
